import { defineComponent } from 'vue';
import Icon from '@/components/icon/Icon.vue';
export default defineComponent({
    name: 'SearchBar',
    components: {
        Icon
    },
    props: {
        modelValue: {
            type: String,
            default: null
        },
        placeholder: {
            type: String,
            default: 'Поиск'
        }
    },
    emits: ['update:modelValue'],
    methods: {
        onInput(value) {
            this.$emit('update:modelValue', value);
        }
    }
});
